#header {
	@include position-fixed-bottom;
	z-index: 10000;
	@media (max-width: 800px) {
		position: fixed;
		right: 0;
		top: 70px;
		width: 100%;
	}
}

.header__inner {
	@include position-center;
	border-radius: 8px;
	background-color: rgba(99, 97, 97, 0.03);
	backdrop-filter: blur(15px);
	padding: 0.5rem 1rem;

	@media (max-width: 800px) {
		position: absolute;
		right: 0;
		left: 320px;
		top: -60px;
		height: 60px;
		transform: translate(0, 0);
	}
}

.header__nav {
	display: flex;
	justify-content: center;
	align-items: center;

	@media (max-width: 800px) {
		display: none;

		&.show {
			display: block;
			position: absolute;
			top: 60px;
			right: 0;
			width: 160%;
			border-radius: 50px 0 0 25px;
			// background-color: var(--mainBackground);

			z-index: 10000;

			ul {
				display: flex;
				flex-direction: column;
				justify-content: flex-end;
				align-items: flex-end;
				padding: 10px 0;
				margin: 0;
				list-style: none;
				text-align: right;

				li {
					padding: 1px;
				}
			}
		}
	}

	ul {
		display: flex;
		list-style: none;
		padding: 0;
		margin: 0;

		li {
			padding: 0 10px;

			a {
				text-transform: uppercase;
				font-size: 14px;
				padding: 10px;
				display: block;
			}
		}
	}
}

.header__nav__mobile {
	display: none;
	width: 40px;
	height: 40px;
	cursor: pointer;

	@media (max-width: 800px) {
		display: block;
	}

	span {
		display: block;
		width: 40px;
		height: 2px;
		background-color: var(--mainColor);
		position: relative;
		margin-top: 15px;
		transition: all 0.3s;

		&::before,
		&::after {
			content: "";
			width: 40px;
			height: 2px;
			background-color: var(--mainColor);
			position: absolute;
			transition: all 0.3s;
		}

		&::before {
			top: -10px;
		}

		&::after {
			bottom: -10px;
		}
	}
}

.header__darkmode-toggle {
	margin-left: auto;
	display: flex;
	align-items: center;
}
